import { useState } from "react";
import "./Contactus.scss";
import { useEffect } from "react";


export default function Contactus() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");

  let handleSubmit = async (e) => {
    e.preventDefault();
    try {
      let res = await fetch("https://contactusserver-deepakhonakeri5.b4a.run/save", {
        method: "POST",
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          name: name,
          email: email,
          message: message,
        }),
      });
      //let resJson = await res.json();
      if (res.status === 200) {
        setName("");
        setEmail("");
	setMessage("");
        alert("Message sent successfully");
      } else {
        alert("Some error occured");
      }
    } catch (err) {
      console.log(err);
    }
  };

useEffect(() => {
  fetch('https://contactusserver-deepakhonakeri5.b4a.run/messages');
}, []);

  return (
    <div className="form">
      <form onSubmit={handleSubmit}>
        
	<input
          class="inputFields"
          type="text"
          value={name}
          placeholder="Name"
          onChange={(e) => setName(e.target.value)}
          required /><br/>
        <input
          class="inputFields"
          type="email"
          value={email}
          placeholder="Email"
          onChange={(e) => setEmail(e.target.value)}
          required/><br/>
        <input
	  type="text"
          class="inputFields"
          value={message}
          placeholder="Message"
          onChange={(e) => setMessage(e.target.value)}
          required/>

        <button type="submit" id="submitButton">Submit</button>
	
      </form>
    </div>
  );
}
